import React, {useEffect} from "react";
import "./App.css";
import { BrowserRouter, Routes, Route } from "react-router-dom";
import { Provider, connect } from 'react-redux';
import store from './redux/redux-store';
import HeaderContainer from './components/Header/HeaderContainer';
import NavContainer from "./components/Navbar/NavContainer";
//import DialogsContainer from "./components/Dialogs/DialogsContainer";      App
//import ProfileContainer from "./components/Profile/ProfileContainer";      App
//import News from "./components/News/News";                                 App
//import Music from "./components/Music/Music";                              App
//import Settings from "./components/Settings/Settings";                     App
//import UserContainer from "./components/Users/UsersContainer";             App
//import Login from "./components/Login/Login";                              App
import { initializeApp } from './redux/app-reducer';
import Preloader from "./components/common/Preloader/Preloader";

// ,    
const DialogsContainer = React.lazy(() => import("./components/Dialogs/DialogsContainer")); // ()     
const ProfileContainer = React.lazy(() => import("./components/Profile/ProfileContainer")); // ()     
const News = React.lazy(() => import("./components/News/News"));                            // ()     
const Music = React.lazy(() => import("./components/Music/Music"));                         // ()     
const Settings = React.lazy(() => import("./components/Settings/Settings"));                // ()     
const UserContainer  = React.lazy(() => import("./components/Users/UsersContainer"));       // ()     
const Login = React.lazy(() => import("./components/Login/Login"));                         // ()     

const App = (props) => {

    useEffect(() => {
        props.initializeApp()
    }, [initializeApp])

    if (!props.initialized) {
        return <Preloader />
    }
    return (
        <div className="app-wrapper">
            <HeaderContainer />
            <NavContainer />
            <div className="app-wrapper-content">
                <React.Suspense fallback={<div>Loading...</div>}> {/*       Suspense*/}
                    <Routes>                                      {/*<div>Loading...</div> -      */}
                        <Route path="/dialogs/*" element=
                            {<DialogsContainer />} />
                        <Route path="/profile/*" element=
                            {<ProfileContainer />}>
                            <Route path=":userIdNew" element=
                                {<ProfileContainer />} />
                        </Route>
                        <Route path="/news" element=
                            {<News />} />
                        <Route path="/music" element=
                            {<Music />} />
                        <Route path="/settings" element=
                            {<Settings />} />
                        <Route path="/friends" element=
                            {<UserContainer />} />
                        <Route path="/login" element=
                            {<Login />} />
                    </Routes>
                </React.Suspense>
            </div>
        </div>
    );
};

const mapStateToProps = (state) => ({
    initialized: state.app.initialized
})

const AppContainer = connect(mapStateToProps, { initializeApp })(App);

const SamuraiJsApp = (props) => {
    return (
        <BrowserRouter>
            <Provider store={store} >
                <AppContainer />
            </Provider>
        </BrowserRouter>
    )
}

export default SamuraiJsApp
